.firstRun {
  position: fixed;

  display: grid;
  grid-template-columns: auto;
  grid-template-rows: auto auto;
  gap: 0px 0px;
  grid-template-areas:
    "first-TopImages"
    "first-Title";
  z-index: 10;
  width: 375px;
  justify-content: center;
  /* margin-top: 150px; */
