  width: 100vw;
  height: 100vh;
  max-width: 1000px;
  max-height: 800px;
  top: 0px;
  left: 0px;
  background-color: white;
  border-bottom-left-radius: 50px;
  border-bottom-right-radius: 50px;
}

.first-TopImages {
  display: grid;
