.winGrid {
  width: 200vw;
  max-width: 1000px;
  margin-left: -50vw;
  height: 100vh;
  max-height: 800px;
  z-index: 3;
  position: fixed;
  left: 0;
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: auto auto auto;
  gap: 0px 0px;
  grid-template-areas:
    "winsTitle"
    "scoreboardTitle"
    "newGameButton";
  justify-content: center;
}

.winsTitle {
  transform: rotate(-16.7deg);
  overflow: hidden;
  display: grid;
  grid-template-columns: auto auto;
  grid-template-rows: auto;
  grid-gap: 0px 0px;
  grid-gap: 0px 0px;
  gap: 0px 0px;
  grid-template-areas: "winnerImage winnerTitle";
  grid-area: winsTitle;
  height: 125px;
  background-color: red;
  margin-top: 82px;
  align-items: center;
  padding-left: 50vw;
  padding-right: 50vw;
}

