#desktopPhoneWrapper {
  position: fixed;
  top: 0;
  left: 0;
  background-color: green;
  width: 100vw;
  height: 100vh;
  background-color: #3b3d44;
}
#greyOutline {
  position: fixed;
  height: 100%;
  width: 100%;
  max-width: 1000px;
  max-height: 800px;
  background-color: #3b3d44;
  /* top: 0; */
  /* left: 0; */
  border-radius: 20px;
  z-index: 0;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
#phoneWrapperImage {
  position: fixed;
  width: 2135px;
  height: 850px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 10000;
  pointer-events: none;
  display: none;
  margin-left: -17px;
}


#whiteInner {
  position: fixed;
  width: 96vw;
  height: 500px;
  max-width: 1000px;
  max-height: 800px;
  top: 8px;
  left: 2vw;
  right: 2vw;
  z-index: 1;
  background-color: white;
  border-bottom-left-radius: 50px;
  border-bottom-right-radius: 50px;
}

#adSpace {
  position: fixed;
  top: 0px;
  left: 0;
  /* height: 155px !important; */
  width: 100vw;
  max-width: 1000px;
  max-height: 800px;
  /* border: 1px solid black; */
  border-top-right-radius: 50px;
  border-top-left-radius: 50px;
  z-index: 2;
  background-color: #3b3d44;
}
/* #adSpace ins{
height: 155px !important;
} */
#rpsSum {
  position: fixed;
  bottom: -3px;
  height: 50px;
  width: 100vw;
  max-width: 1000px;
  max-height: 800px;
  left: 0;
  text-align: center;
  z-index: 1;
  color: white;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr;
  gap: 0px 0px;
  grid-template-areas: "rockImg rockCounter paperImage paperCounter scissorsImage scissorsCounter";
  border-top: 2px solid white;
}
#rpsSum img {
  width: 40px;
  height: 40px;
}
ul {
  display: inline-flex;
  width: 100vw;
  max-width: 1000px;
  max-height: 800px;
  justify-content: space-evenly;
  padding-left: 0;
  font-family: Overpass;
  font-style: normal;
  font-weight: 900;
  font-size: 16px;
  line-height: 28px;
  margin-top: 3px;
  list-style: none;
  margin-left: -20px;
}
li {
  width: 300px;
}
#rockNum {
  font-family: Overpass;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  color: #ffffff;
  margin-top: 10px;
  margin-bottom: 10px;
}
#paperNum {
  font-family: Overpass;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  color: #ffffff;
  margin-top: 10px;
  margin-bottom: 10px;
}
#scissorNum {
  font-family: Overpass;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  color: #ffffff;
  margin-top: 10px;
  margin-bottom: 10px;
}

.rockImg {
  grid-area: rockImg;
}

.rockCounter {
  grid-area: rockCounter;
}

.paperImage {
  grid-area: paperImage;
}

.paperCounter {
  grid-area: paperCounter;
}

.scissorsImage {
  grid-area: scissorsImage;
}

.scissorsCounter {
  grid-area: scissorsCounter;
}

#openSettingsButton {
  width: 60px;
  height: 60px;
  position: fixed;
  top: 320px;
  z-index: 40;
  border: 2px solid white;
  border-radius: 30px;
  overflow: hidden;
  background-color: #3b3d44;
  left: -28px;
}
#settingsImage {
  height: 30px;
  width: 30px;
  margin-top: 15px;
  margin-right: -13px;
}
#bottomClickInfo {
  font-size: 12px;
  text-align: center;
  height: 0px;
  margin-left: -128px;
  width: 0px;
  white-space: nowrap;
  font-weight: unset;
}
